@import url(./fonts/stylesheet.css);

body {
    background-color: #9CA3AF;
    font-family: 'Roboto Flex', 'Laqonic 4F Unicase', sans-serif;
    color: #FFFFFF;
    min-width: 320px;
    max-width: 1600px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
}




.header {
    display: flex;
    justify-content: space-between;
    margin: 72px 96px 0;
    align-items: center;
}

.header__title {
    font-family: 'Laqonic 4F Unicase';
    width: 146px;
    height: 84px;
    line-height: 42px;
    font-size: 36px;
    font-weight: 700;
}

.header__right {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    list-style: none;
    margin: 0;
    gap: 32px;
    padding: 0;
    display: flex;
    align-items: center;
}

.header__lang {
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 44px;
}

.header__search {
    background: url("./images/поиск.svg") no-repeat center;
    width: 46px;
    height: 46px;
}

.header__burger {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.main {
    display: flex;
    flex-direction: column;
    margin: 0 90px 0 96px;
}

.main__name {
    margin: 245px 0 163px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 16px;
    z-index: 5;

}

.main__name_text {
    font-family: 'Laqonic 4F Unicase';
    font-size: 96px;
    font-style: normal;
    font-weight: 900;
    line-height: 112px;
    margin: 0;
    text-align: left;
    width: 462px;
    z-index: 5;

}

.main__subtitle {
    font-family: 'Roboto Flex';
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 70px;
    width: 100%;
    border-bottom: 2px solid white;
    padding-bottom: 16px;
    margin: 0;
    z-index: 4;
    position: relative;
}

.main__text {
    width: 274px;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px;
    margin: 0;
    z-index: 5;

}

.main__text_container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin: 40px 0;
    z-index: 5;

}

.main__image {
    position: absolute;
    top: 17px;
    align-self: center;
}







.menu__title {
    margin-top: 120px;
    font-family: 'Roboto Flex';
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: 134px;
}

.menu__links {
    margin: 56px 0 104px;
    padding: 0;
    width: 786px;
    gap: 24px;
    list-style-type: none;
    display: flex;
    flex-direction: column;
}

.menu__link {
    font-family: 'Roboto Flex';
    font-size: 56px;
    font-style: normal;
    font-weight: 700;
    line-height: 62px;
    padding: 0 0 24px 0;
    border-bottom: 1px solid #FFF;
    text-decoration: none;
    color: #FFF;
}

.menu__header {
    margin-left: 0;
    margin-right: 0;
}

.art {
    margin-top: 120px;
}

.art__description {
    position: absolute;
    z-index: 5;
    top: 917px;
    right: 96px;
    width: 1000px;
    padding: 72px 120px;
    display: flex;
    background: #9CA3AF;
    flex-direction: column;
}

.art__title {
    font-size: 36px;
    font-style: normal;
    font-weight: 900;
    line-height: 42px;
    margin: 0 0 56px 0;
}

.art__image {
    width: 100%;
    height: 796px;
}

.art__name {
    width: 844px;
    font-family: 'Laqonic 4F Unicase';
    font-size: 96px;
    font-style: normal;
    font-weight: 900;
    line-height: 112px;
    margin: 0 0 85px 0;
}

.art__text {
    width: 518px;
    height: 140px;
    font-family: 'Roboto Flex';
    font-size: 35px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    margin: 0;
}

.art__text_container {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
}

.artcard {
    margin: 435px 96px 0 96px;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;
    column-gap: 34px;
    row-gap: 96px;
}

.card {
    width: 456px;
}

.card__pic {
    width: 454px;
    height: 655px;
    background-color: #FFF;
}

.card__image {
    margin: 32px;
}

.circles {
    display: flex;
    justify-content: center;
    flex-direction: row;
    gap: 8px;
}

.card__circle {
    position: relative;
    z-index: 5;
    background-color: black;
    width: 20px;
    height: 20px;
    display: block;
    border-radius: 100%;
}

.card__description {
    width: 454px;
}

.card__title {
    font-family: 'Roboto Flex';
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    margin: 24px 0 16px 0;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.45);
}

.card__text {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    font-family: 'Roboto Flex';
    margin: 0;
}

.card__size {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    font-family: 'Roboto Flex';
    margin: 8px 0 16px 0;
}

.card__price {
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    margin: 0;
    font-family: 'Roboto Flex';
}

.contact {
    background-image: url(./images/contact-pic.png);
    background-repeat: no-repeat;
    background-position: bottom right;
    padding: 0 96px;
    font-family: 'Roboto Flex';
    
}

.contact__title {
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: 134px;
    margin: 120px 0 72px;
}

.contact__content {
    display: flex;
    flex-direction: row;
    gap: 96px;
    margin: 0;
}

.contact__column {
    width: 386px;
}

.column {
    list-style-type: none;
    padding: 0;
}


.contact__column_title {
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 48px;
    padding-bottom: 16px;
    border-bottom: 1px solid white;
    margin-top: 32px;
}

.column__name {
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    margin-top: 12px;
}

.column__adress {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__tc {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__phone {
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    margin-top: 12px;
}

.column__email {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__grafik {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    margin-top: 12px;
}

.column__contact {
    display: flex;
    flex-direction: row;
    width: 88px;
    justify-content: space-between;
    margin-top: 26px;
}

.column__icon {
    width: 32px;
    height: 32px;
}