    .mobile_btn {
      display: block;
      cursor: pointer;
      z-index: 10;
    }
  
    .menu {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        height: 100%;
        background: #9CA3AF;
        padding: 0;
        transform: translateX(100%);
        transition: transform 0.5s;
        z-index: 10;
        padding: 0  90px;
        background-image: url(../images/menu-pic.png);
        background-repeat: no-repeat;
        background-position: bottom right;
    }
  
    .active {
        transform: translateX(0%);

    }
  
